.page {
    height: 100vh;
    position: absolute;
    width: 100%;
    touch-action: none;
  }
  
  .page > div {
    touch-action: none;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 100%;
    height: 100%;
  }
  
  .wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .drag{
    touch-action: none;
  }
  